.home-slick {
  padding: 5rem;
  background: #e6e6e6;
}

.contain p {
  padding: 5rem;
  font-size: 20px;
  text-align: justify;
  align-items: center;
  justify-content: center;
}

.home-slick h1 {
  text-align: center;
  font-size: 40px;
  font-family: "Fuggles", cursive;
}

.slick-text {
  border: 2px solid;
  background-color: #fff;
  border-color: rgba(255, 255, 255, 0.1);
}

@media all and (max-width: 800px) {
  .contain p {
    padding: 0.8rem;
    font-size: 12px;
  }

  .home-slick h1 {
    font-size: 35px;
  }

  .slick-text p {
    padding: 3px;
    margin-left: 45px;
    margin-right: 45px;
  }
}

@media all and (max-width: 450px) {
  .home-slick {
    padding: 2rem;
  }

  .slick-text p {
    padding: 3px;
    margin-left: 45px;
    margin-right: 45px;
  }
}

@media all and (max-width: 550px) {
  .home-slick h1 {
    font-size: 25px;
  }
}
